import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { GlobalService } from "../service/global.service";
import { UserService } from "../service/user.service";
import Swal from "sweetalert2";
@Component({
  selector: "app-orders",
  templateUrl: "./orders.component.html",
  styleUrls: ["./orders.component.css"],
})
export class OrdersComponent implements OnInit {
  customerName: any;
  menuData: any = [];
  cartItems: any = [];
  subtotal: number = 0;
  totalamount: number = 0;
  TableCode: any = "";
  tax: number = 200;
  loading: boolean;
  res: any = [];
  orderID: any = "";
  disableTableNo : boolean = false
  constructor(
    private router: Router,
    private _globalService: GlobalService,
    private _userService: UserService
  ) {}

  ngOnInit() {
    this.disableTableNo = false
    if (localStorage.getItem("otpStatus") == null) {
      return this.router.navigate(["/"], { queryParamsHandling: "preserve" });
    }
    this.menuData = JSON.parse(sessionStorage.getItem("menuData"));

    if (sessionStorage.getItem("TableCode")) {
      this.TableCode = sessionStorage.getItem("TableCode");
      this.disableTableNo = true
    }
    this.fetchMenu();
    this.customerName = localStorage.getItem("customerName");
  }

  resetCartItems() {
    this.cartItems = []
    this.totalamount = 0;
    this.menuData = JSON.parse(sessionStorage.getItem("menuData"));
    for (let i = 0; i < this.menuData.length; i++) {
      for (let j = 0; j < this.menuData[i].Items.length; j++) {
        if (this.menuData[i].Items[j].Item_UOM[0].Qty > 0) {
          this.menuData[i].Items[j].Item_UOM[0].Qty = 0
        }
      }
    }
    sessionStorage.setItem("menuData", JSON.stringify(this.menuData))
  }

  fetchMenu() {
    this.cartItems = [];
    this.menuData = JSON.parse(sessionStorage.getItem("menuData"));
    for (let i = 0; i < this.menuData.length; i++) {
      for (let j = 0; j < this.menuData[i].Items.length; j++) {
        if (this.menuData[i].Items[j].Item_UOM[0].Qty > 0) {
          this.cartItems.push({
            SitemCode: this.menuData[i].Items[j].SitemCode,
            Qty: this.menuData[i].Items[j].Item_UOM[0].Qty,
          });
          this.totalamount +=
            this.menuData[i].Items[j].Item_UOM[0].Rate *
            this.menuData[i].Items[j].Item_UOM[0].Qty;
        }
      }
    }
    if (!this.cartItems.length) {
      this.router.navigate(["/menu"], {
        queryParamsHandling: "preserve",
        replaceUrl: true
      })
      return;
    }

    this.subtotal = this.totalamount;
    // this.totalamount +=this.tax
    if (this.totalamount === 0) {
      return this.router.navigate(["/menu"], {
        queryParamsHandling: "preserve",
      });
    }
  }
  menuRedirect() {
    this.router.navigate(["/menu"], { queryParamsHandling: "preserve" });
  }
  redirect() {
    if (
      this.TableCode === undefined ||
      this.TableCode === null ||
      this.TableCode === ""
    ) {
      Swal.fire("Oops...", "Please enter table no !", "error");
    } else {
      this.loading = true;

      this.orderID = sessionStorage.getItem("orderID");
      console.log(this.orderID);

      if (this.orderID === null) {
        let body = {
          TableCode: this.TableCode,
          FloorLocation: sessionStorage.getItem("floorCode"),
          CustPhone: localStorage.getItem("customerPhone"),
          CustomerName: localStorage.getItem("customerName"),
          OrderItems: this.cartItems,
        };
        this._userService.placeOrder(body).subscribe({
          next: (data) => {
            this.res = data;
            this.resetCartItems()
            console.log(this.res);
            if (this.res.error == false) {
              sessionStorage.setItem("TableCode", this.TableCode);
              sessionStorage.setItem("orderID", this.res.data);
              this.loading = false;
              this.router.navigate(["/feedback"], {
                queryParamsHandling: "preserve",
                replaceUrl:true
              });
            }
          },
          error: (error) => {
            this.loading = false;
            if (error.status == 403) {
              localStorage.clear();
              sessionStorage.clear();
              Swal.fire(
                "Oops...",
                "Session Expired ! Please login again.",
                "error"
              );
              this.router.navigate(["/"], { queryParamsHandling: "preserve" });
            } else {
              Swal.fire("Oops...", `${error.error.info}`, "error");
            }
          },
        });
      } else {
        let bodyData = {
          OrderNumber: this.orderID,
          OrderItems: this.cartItems,
        };
        this._userService.modifyOrder(bodyData).subscribe({
          next: (data) => {
            this.res = data;
            this.resetCartItems()
            if (this.res.error == false) {
              sessionStorage.setItem("orderID", this.res.data);
              this.loading = false;
              this.router.navigate(["/feedback"], {
                queryParamsHandling: "preserve",
                replaceUrl: true
              });
            }
          },
          error: (error) => {
            this.loading = false;
            if (error.status == 403) {
              localStorage.clear();
              sessionStorage.clear();
              Swal.fire(
                "Oops...",
                "Session Expired ! Please login again.",
                "error"
              );
              this.router.navigate(["/"], { queryParamsHandling: "preserve" });
            } else {
              console.log(error.error);
              Swal.fire("Oops...", `${error.error.info}`, "error");
            }
          },
        });
      }
    }
  }

  increaseQnt(SitemCode, SubCategoryCode) {
    if (this.loading) {
      return;
    }

    this.totalamount = 0;
    for (let i = 0; i < this.menuData.length; i++) {
      for (let j = 0; j < this.menuData[i].Items.length; j++) {
        if (
          this.menuData[i].Items[j].SitemCode === SitemCode &&
          this.menuData[i].Items[j].SubCategoryCode === SubCategoryCode
        ) {
          this.menuData[i].Items[j].Item_UOM[0].Qty += 1;
          sessionStorage.setItem("menuData", JSON.stringify(this.menuData));
          this.fetchMenu();
        }
      }
    }
  }
  decreaseQnt(SitemCode, SubCategoryCode) {
    if (this.loading) {
      return;
    }

    this.totalamount = 0;
    for (let i = 0; i < this.menuData.length; i++) {
      for (let j = 0; j < this.menuData[i].Items.length; j++) {
        if (
          this.menuData[i].Items[j].SitemCode === SitemCode &&
          this.menuData[i].Items[j].SubCategoryCode === SubCategoryCode
        ) {
          if (this.menuData[i].Items[j].Item_UOM[0].Qty == 0) {
            break;
          }
          this.menuData[i].Items[j].Item_UOM[0].Qty -= 1;
          sessionStorage.setItem("menuData", JSON.stringify(this.menuData));
          this.fetchMenu();
        }
      }
    }
  }

  logout(){
    Swal.fire({
      title: "Logout",
      text: "Are you sure you want to logout ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: 'Yes, Logout'
    })
    .then((result) => {
      if (result.value) {
        localStorage.clear();
        sessionStorage.clear();
        this.router.navigate(['/'], {queryParamsHandling : 'preserve'});
      }
    });
  }
}
